/* ============================================ */
/**
1. Correct table border color inheritance in all Chrome and Safari.
*/
table {
  border-color: inherit; /* 1 */
  border-collapse: collapse;
}

caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.header {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 4.625rem;
  background-color: var(--main-color);
}

.header-body {
  padding: 1.4375rem clamp(0.9375rem, -0.6201923077rem + 6.6461538462vw, 7.6875rem) clamp(1.4375rem, 1.3798076923rem + 0.2461538462vw, 1.6875rem) clamp(0.9375rem, -0.6201923077rem + 6.6461538462vw, 7.6875rem);
}

.header-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 41.5rem;
  background-color: var(--main-color);
  transform: translateY(-100%);
  transition: transform 0.5s;
}
@media (min-width: 992px) {
  .nav {
    position: static;
    width: auto;
    height: auto;
    transform: none;
    background-color: transparent;
  }
}

.nav-list {
  padding-top: 12.9375rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 992px) {
  .nav-list {
    padding-top: 0;
    flex-direction: row;
  }
}

.nav-item:not(:first-of-type) {
  margin-top: 3.125rem;
}
@media (min-width: 992px) {
  .nav-item:not(:first-of-type) {
    margin: 0 0 0 3.375rem;
  }
}

.nav-link {
  font-weight: 500;
  color: var(--base-color);
  line-height: 1.5;
}

.nav.is-active {
  transform: none;
}

.hamburger-menu {
  position: fixed;
  top: 1.4375rem;
  right: clamp(0.9375rem, -0.6201923077rem + 6.6461538462vw, 7.6875rem);
  z-index: 3;
  width: 1.875rem;
  height: 1.875rem;
  cursor: pointer;
}

.hamburger-menu-line {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 1.5625rem;
  height: 0.125rem;
  background-color: var(--base-color);
  transition: 0.3s;
}

.hamburger-menu-line:nth-child(1) {
  top: calc(50% - 8px);
}

.hamburger-menu-line:nth-child(3) {
  top: calc(50% + 8px);
}

.hamburger-menu.is-active > .hamburger-menu-line:nth-child(1) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
}

.hamburger-menu.is-active > .hamburger-menu-line:nth-child(2) {
  opacity: 0;
}

.hamburger-menu.is-active > .hamburger-menu-line:nth-child(3) {
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.footer {
  margin-top: 3.125rem;
  background-color: var(--main-color);
}

.footer-body {
  padding: clamp(0.9375rem, 0.8221153846rem + 0.4923076923vw, 1.4375rem) 0 clamp(0.9375rem, 0.7788461538rem + 0.6769230769vw, 1.625rem) 0;
}

.footer-copy {
  display: block;
  text-align: center;
  font-size: 0.875rem;
  line-height: 1.875rem;
  color: var(--base-color);
}

.container {
  max-width: 64.375rem;
  margin: 0 auto;
  padding: 0 1.25rem;
}

.sp-only {
  display: block;
}
@media (min-width: 992px) {
  .sp-only {
    display: none;
  }
}

.btn {
  display: inline-block;
  font-family: "Inter", sans-serif; /* Interフォントを適用 */
  padding: 0.9375rem 0;
  font-size: 1.25rem;
  line-height: 1.5;
  letter-spacing: 0.05em;
  width: 14.6875rem;
  border-radius: 3.125rem;
}

.btn--submit {
  color: var(--base-color);
  background-color: var(--main-color);
}

.card-body {
  margin-top: 4.4375rem;
}

.card-wrapper {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(1, 1fr);
  row-gap: 3.125rem;
}
@media (min-width: 768px) {
  .card-wrapper {
    grid-template-columns: repeat(2, 1fr);
    column-gap: 2.125rem;
  }
}
@media (min-width: 1100px) {
  .card-wrapper {
    grid-template-columns: repeat(3, 1fr);
    row-gap: 0;
  }
}

.card {
  background-color: var(--base-color);
  box-shadow: 0px 0px 3px 0px rgba(27, 19, 16, 0.4);
}

.card-image {
  width: 100%;
  object-fit: cover;
}

.card-image > img {
  width: 100%;
}

.card-text-body {
  padding: 1.375rem 2rem 1.8125rem 2rem;
}

.card-text {
  font-size: 0.8125rem;
  font-family: "Inter", sans-serif; /* Interフォントを適用 */
  line-height: 1.5625rem;
}

.section-title {
  font-size: 2.25rem;
  font-weight: 500;
  text-align: center;
  letter-spacing: 0.05em;
}

.fv {
  margin-top: 4.625rem;
  position: relative;
  background-image: url(../img/fv-bgi.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  height: clamp(36.875rem, 34.7115384615rem + 9.2307692308vw, 46.25rem);
}

.fv-title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: clamp(1.875rem, 1.8028846154rem + 0.3076923077vw, 2.1875rem) clamp(2.5rem, 2.4711538462rem + 0.1230769231vw, 2.625rem);
  font-size: clamp(1.875rem, 1.5865384615rem + 1.2307692308vw, 3.125rem);
  font-weight: 500;
  color: var(--base-color);
  border: 0.125rem solid var(--base-color);
  letter-spacing: 0.05em; /* em単位ならフォントサイズの割合で反映されます */
}

.about {
  margin-top: 6.375rem;
}

.about-body {
  margin-top: 4.3125rem;
}

.about-title {
  font-size: clamp(1.25rem, 1.1923076923rem + 0.2461538462vw, 1.5rem);
  font-family: "Inter", sans-serif; /* Interフォントを適用 */
  line-height: 2.5rem;
}

.about-text-body {
  margin-top: clamp(1.125rem, 0.9807692308rem + 0.6153846154vw, 1.75rem);
}

.about-text {
  font-size: 0.875rem;
  font-family: "Inter", sans-serif; /* Interフォントを適用 */
  line-height: 1.875rem;
  letter-spacing: 0.05em;
}

.service {
  margin-top: 9.875rem;
}

.service-body {
  margin-top: clamp(4.3125rem, 4.2403846154rem + 0.3076923077vw, 4.625rem);
}

.service-wrapper {
  display: grid;
  row-gap: clamp(2.8125rem, 2.7403846154rem + 0.3076923077vw, 3.125rem);
}

.service-box {
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 992px) {
  .service-box {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (min-width: 992px) {
  .service-image {
    flex: 1 1 auto; /* grow = 1, shrink = 1, basis = 200px */
    width: 50%;
    margin-right: clamp(2.1875rem, 0.0347222222rem + 3.4722222222vw, 4.375rem);
  }
}

.service-desc {
  margin-bottom: 2.8125rem;
}
@media (min-width: 992px) {
  .service-desc {
    margin-bottom: 0;
    width: 50%;
  }
}

@media (min-width: 992px) {
  .service-box:nth-child(2) {
    flex-direction: row-reverse;
  }
}

@media (min-width: 992px) {
  .service-box:nth-child(2) .service-image {
    margin-right: 0;
    margin-left: clamp(2.1875rem, 0.0347222222rem + 3.4722222222vw, 4.375rem);
  }
}

.service-title {
  font-size: clamp(1.25rem, 1.1923076923rem + 0.2461538462vw, 1.5rem);
  font-family: "Inter", sans-serif; /* Interフォントを適用 */
  line-height: clamp(2.5rem, 2.3846153846rem + 0.4923076923vw, 3rem);
  letter-spacing: 0.1em;
}

.service-text-body {
  margin-top: clamp(1.125rem, 0.9807692308rem + 0.6153846154vw, 1.75rem);
}

.service-text {
  font-size: 0.875rem;
  line-height: 1.875rem;
  letter-spacing: 0.05em;
}

.news {
  margin-top: 9.3125rem;
}

.contact {
  margin-top: 9rem;
}

.contact-title {
  text-align: center;
  font-size: 1.5rem;
  font-family: "Inter", sans-serif; /* Interフォントを適用 */
  line-height: 2;
  letter-spacing: 0.1em;
}

.form-body {
  margin-top: 3.5rem;
}

.form {
  display: grid;
  row-gap: 1.0625rem;
}

.form-title {
  font-family: "Inter", sans-serif; /* Interフォントを適用 */
  line-height: 1.875rem;
  letter-spacing: 0.05em;
}

input[type=text],
input[type=email],
input[type=tel],
textarea {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  border: 1px solid var(--text-color);
}

input[type=text],
input[type=email],
input[type=tel] {
  height: 1.875rem;
}

textarea {
  height: 10.6875rem;
}

.form-btn {
  margin-top: 3.125rem;
  text-align: center;
}

:root {
  --main-color: #1B1310;
  --text-color: #141414;
  --base-color: #fff ;
}